.footer {
	background-color: $bgn-color;
}

.footer-wraper {
	display: flex;
	justify-content: center;
	align-items: center;
	padding-top: 30px;
	padding-bottom: 30px;

	@include font(400, 14, 16);
	color: $third-color;

	@media screen and (min-width: 768px) {
		@include font(400, 16, 19);
	}

	@media screen and (max-width: 768px) {
		flex-wrap: wrap;
	}

	&-heart-icon {
		fill: $accent-color;
		margin-right: 10px;
		margin-left: 10px;

		animation: pulse 1500ms infinite;
		transition: $transition;
		@keyframes pulse {
			0% {
				transform: scale(1);
			}

			10% {
				transform: scale(1.3);
			}

			40% {
				transform: scale(0.9);
			}

			50% {
				transform: scale(1.3);
			}
		}
	}
	.footer-text-mrbottom {
		@media screen and (max-width: 469px) {
			margin-bottom: 10px;
		}
	}
	.footer-text-mrleft {
		margin-left: 5px;

		.footer-link {
			color: $third-color;
			text-decoration: underline;
			transition: $transition;
			&:hover,
			&:focus {
				color: $accent-color;
			}
		}
	}
}
