.grid-container {
   width: 100%;
   display: grid;
   gap: 20px;
   padding: 20px 0;

   @media screen and (min-width: 768px) {
      grid-template-columns: 1fr 1fr;
      gap: 32px;
      padding: 60px 0;
   }

   @media screen and (min-width: 1024px) {
      grid-template-columns: 1fr 1fr 1fr;
      row-gap: 32px;
      column-gap: 16px;
      padding: 60px 0;
   }
}
